@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');

/*main-color - #202444*/

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
    -webkit-user-drag: none;
    overflow-x: hidden;
    font-family: Poppins;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button {
    -webkit-tap-highlight-color: transparent;
}

html, body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.App {
    width: 100vw;
    height: 100vh;
}

.app_title {
    font-size: 45px;
}

.main_vector {
    width: 450px;
    margin-bottom: -55px;
}

.app_description {
    margin: auto;
    margin-top: 5px;
    max-width: 420px;
}

.block {
    transform: translateY(-20px);
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.main_next_button {
    padding: 9px;
    border-radius: 10px;
    border: none;
    background-color: #088cec;
    color: white;
    font-size: 17px;
    width: 82%;
    text-decoration: none;
    margin: auto;
    margin-top: 15px;
    outline: none;
    transition: all 0.2s;
    cursor: pointer;
}

.main_next_button:hover {
    transition: all 0.2s;
    transform: translateY(-2px);
    box-shadow: 0px 10px 10px rgba(8, 140, 236, 0.13);
}

.main_next_button:active {
    transition: all 0.2s;
}

.spheres_vector {
    width: 450px;
}

.category_icon {
    width: 40px;
    height: 40px;
}

.categories {
    width: fit-content;
    height: 110px;
    display: flex;
    margin: auto;
    display: flex;
    align-items: end;
    gap: 18px;
    margin-top: 5px;
}

.category {
    background-color: #f1f1f1;
    padding: 12px;
    height: fit-content;
    width: 110px;
    border-radius: 7px;
    cursor: pointer;
    transition: transform 0.3s;
}

.category:hover {
    transition: transform 0.3s;
    transform: translateY(-5px);
}

.line-bg {
    width: 90%;
    height: 15px;
    border-radius: 10px;
    background-color: #bbbbbb;
    overflow: hidden;
    margin-left: 11px;
}

.quiz-line {
    height: 100%;
    transition: width 0.3s;
    border-radius: 10px;
    background-image: radial-gradient( circle farthest-corner at 92.3% 71.5%,  rgba(83,138,214,1) 0%, rgba(134,231,214,1) 90% );
}

.line-block {
    display: flex;
    width: 95%;
    height: 15px;
    gap: 10px;
    margin: auto;
    margin-left: 0px;
    align-items: center;
    overflow: hidden;
}

.question-block {
    width: 500px;
    transform: translateY(0px);
}

.question-title {
    font-size: 38px;
    text-align: start;
    margin-left: 10px;
    margin-top: 18px;
    margin-bottom: 13px;
}

.options {
    text-align: start;
    margin-left: 10px;
    width: 100%;
    margin-top: 10px;
    height: fit-content;
    display: flex;
    justify-content: start;
    flex-direction: column;
    gap: 10px;
}

.option {
    border: 2px solid gray;
    width: 90%;
    padding: 10px;
    cursor: pointer;
    border-radius: 10px;
    transition: all 0.2s;
}

@media only screen and (max-width: 600px) {
    .question-block {
        transform: scale(0.9) translateX(12px);
        margin-left: auto;
        margin-right: auto;
    }
}

@media only screen and (max-width: 450px) {
    .block {
        transform: scale(0.9);
    }
}

@media only screen and (max-width: 435px) {
    .block {
        transform: scale(0.86);
    }
}

@media only screen and (max-width: 425px) {
    .block {
        transform: scale(0.82);
    }
}

@media only screen and (max-width: 420px) {
    .block {
        width: 400px;
    }

    .main_vector {
        margin-left: -20px;
    }

    .question-block {
        transform: scale(0.82) translateX(10px);
    }
}

@media only screen and (max-width: 385px) {
    .block {
        width: 370px;
        transform: scale(0.75);
    }

    .main_vector {
        margin-left: -40px;
    }
}

@media only screen and (max-width: 330px) {
    .block {
        width: 350px;
        margin-left: -10px;
    }

    .main_vector {
        width: 420px;
        margin-left: -35px;
    }
}

@media only screen and (max-height: 750px) {
    .block {
        margin-top: 50px;
    }
}

@media only screen and (max-height: 650px) {
    .block {
        margin-top: 10px;
    }
}